@font-face{font-family:"MontserratAlternates";src:url("../resources/fonts/MontserratAlternates-Light.ttf")}@font-face{font-family:"小orinthia";src:url("../resources/fonts/小orinthia.ttf")}.navigation{background:linear-gradient(to top, rgba(47, 60, 60, 0.7), rgba(67, 103, 103, 0.8));left:30px;right:30px;height:60px;bottom:14px;position:fixed;border-radius:5px;padding:3px 10px;animation:showNavigation 1s}.navigation_logo{height:100%;opacity:.3;pointer-events:none}.navigation_bg{position:absolute;left:0;right:0;height:98%;border-radius:5px;background:url("../resources/bgn.jpg");opacity:.08;z-index:-1}.navigation_date{margin:auto 0;color:#fff;text-shadow:2px 2px 1px #000}.navigation_controls{padding:5px 0 2px;white-space:nowrap;width:calc(100% - 150px)}.navigation_link{background-color:rgba(76,116,116,.6);color:#fff;display:inline-block;text-align:center;font-size:18px;border-radius:3px;border:.5px solid rgba(255,255,255,.2);text-decoration:none;text-shadow:2px 2px 1px #000;box-shadow:-2px -2px 1px rgba(0,0,0,.3);padding:4px 2px;width:25%;height:100%;margin:0 10px;transition:.3s}.navigation_link:hover{background-color:rgba(82,184,143,.7)}.navigation_activelink{width:50%;background:linear-gradient(to top, rgba(48, 162, 33, 0.7), rgba(116, 167, 109, 0.5))}.navigation_led{width:18px;height:7px;margin:5px auto;border-radius:3px;background-color:rgba(160,241,241,.3);box-shadow:-2px -2px 1px rgba(0,0,0,.3);transition:.5s}.navigation_led_active{width:80%;height:7px;margin:5px auto;border-radius:3px;background:radial-gradient(rgb(184, 250, 190), rgb(42, 255, 4));box-shadow:-2px -2px 4px rgba(0,0,0,.3);transition:1s}@keyframes showNavigation{from{bottom:-20vh}to{bottom:14px}}@media(max-width: 850px){.navigation_link{font-size:16px;margin:0 5px}}@media(max-width: 600px){.navigation_link{font-size:9px;margin:0 3px}.navigation_date{font-size:10px}.navigation_controls{width:calc(100% - 120px)}.navigation{left:5px;right:5px;padding:3px 5px}.navigation_led{margin:12px auto}.navigation_led_active{margin:12px auto}}@media(max-width: 400px){.navigation_link{font-size:7px}}.main{height:calc(100% - 85px);width:100%;animation:showTop 1s;position:relative}.main_title{font-size:40px;color:#fff;text-shadow:2px 2px 2px #000}.main_description{font-family:"小orinthia","MontserratAlternates";margin-top:20px;color:#fff;font-size:38px;letter-spacing:5px;text-shadow:2px 2px 1px #000}.main_description_buf{margin-top:120px}.main_line{margin:30px;width:5px;border-radius:5px;box-shadow:0 0 5px #fff;background:radial-gradient(rgb(255, 255, 255), rgb(229, 243, 129))}.main_robo{margin:30px auto;width:350px;height:400px;background:url("../resources/robo.svg") no-repeat;background-size:cover;filter:drop-shadow(0 0 18px #000);position:relative}.main_robo_block{margin:auto 0}.main_robo_hand{width:150px;height:95px;background:url("../resources/roboHand.svg") no-repeat;background-size:cover;filter:drop-shadow(0 0 18px #000);top:117px;left:20px;position:absolute;transform-origin:100% 100%;animation:handRotate 1.5s infinite}.main_robo_ball{position:absolute;top:200px;left:160px;width:14px;height:16px;border-radius:10px;background-color:#9c9e9e;box-shadow:-1px 0 1px #000}.main_center{display:flex;justify-content:center}@keyframes showTop{from{top:-120vh}to{top:0}}@keyframes handRotate{0%,100%{transform:rotate(-15deg)}50%{transform:rotate(-2deg)}}@media(max-width: 1000px){.main_description{font-size:30px}.main_description_buf{margin-top:90px}}@media(max-width: 850px){.main_robo{width:250px;height:280px}.main_robo_hand{width:100px;height:70px;top:83px;left:18px}.main_robo_ball{top:141px;left:110px;height:14px}.main_title{font-size:30px}}@media(max-width: 550px){.main_title{font-size:16px;text-align:center}.main_center{display:block}.main_description{font-size:25px;text-align:center}.main_description_buf{margin-top:30px}}*{font-family:"MontserratAlternates",sans-serif;letter-spacing:.5px;padding:0;margin:0;box-sizing:border-box;user-select:none}body{font-size:16px}.app{height:100vh;width:100vw;overflow:hidden;position:fixed;background-color:#1a1e1e}.app_backimage{background:url(../resources/bg.jpg) repeat;width:100vw;height:200vh;opacity:.03;position:absolute;animation:bgTranc 45s infinite linear;z-index:-2}.bgcontainer{z-index:-5}.flex{display:flex}.flex_between{display:flex;justify-content:space-between}@keyframes bgTranc{0%{transform:translateY(0)}100%{transform:translateY(-50%)}}